import dagre from "dagre";
import { Node, Edge, Position } from "react-flow-renderer";
import { MarkerType } from "@xyflow/react";

const nodeWidth = 150;
const nodeHeight = 50;

export const getLayoutedNodesAndEdges = (
  nodes: Node[],
  edges: Edge[],
  direction: "TB" | "LR" = "TB"
): { nodes: Node[]; edges: Edge[]; columnPositions: number[] } => {
  const dagreGraph = new dagre.graphlib.Graph();
  dagreGraph.setGraph({
    rankdir: direction,
    nodesep: 50,
    edgesep: 50,
    ranksep: 100,
  });
  dagreGraph.setDefaultEdgeLabel(() => ({}));

  nodes.forEach((node) => {
    dagreGraph.setNode(node.id, { width: nodeWidth, height: nodeHeight });
  });

  edges.forEach((edge) => {
    dagreGraph.setEdge(edge.source, edge.target);
  });

  dagre.layout(dagreGraph);

  const layoutedNodes = nodes.map((node) => {
    // const nodeWithPosition = dagreGraph.node(node.id);
    // dagre  y   x     
    const fixedX = node.position.x; //    x  
    // const computedY = nodeWithPosition.y; // dagre  y 
    const fixedY = node.position.y;

    return {
      ...node,
      position: { x: fixedX, y: fixedY },
      sourcePosition: Position.Right,
      targetPosition: Position.Left,
    };
  });

  const layoutedEdges = edges.map((edge) => {
    const sourceNode = layoutedNodes.find((n) => n.id === edge.source);
    const targetNode = layoutedNodes.find((n) => n.id === edge.target);

    if (!sourceNode || !targetNode) return edge;

    //      
    const sourceX = sourceNode.position.x + nodeWidth / 2;
    const sourceY = sourceNode.position.y;

    const targetX = targetNode.position.x - nodeWidth / 2;
    const targetY = targetNode.position.y;

    //   
    const midX = sourceX + (targetX - sourceX) * 0.2; // X 20% 
    const midY = sourceY + (targetY - sourceY) * 0.5; // Y  
    const points = [
      { x: sourceX, y: sourceY }, // 
      { x: midX, y: sourceY }, //  
      { x: midX, y: midY }, //  
      { x: targetX, y: targetY }, // 
    ];

    // const offset = 50; //   
    // const midX = sourceX + offset;
    // const points = [
    //   { x: sourceX, y: sourceY }, // 
    //   { x: midX, y: sourceY }, //    
    //   { x: midX, y: targetY }, //    
    //   { x: targetX, y: targetY }, // 
    // ];

    return {
      ...edge,
      data: { points }, //   
      markerEnd: {
        type: MarkerType.ArrowClosed,
        width: 20,
        height: 20,
      },
    };
  });

  const allX = nodes.map((node) => node.position.x - 300);
  const columnPos = new Set<number>(allX);
  const columnPositions = Array.from(columnPos).sort((a, b) => a - b);

  return { nodes: layoutedNodes, edges: layoutedEdges, columnPositions };
};
